import React, { useEffect, useState } from "react";

import "./location.styles.scss";

import Weather from "../weather/weather.component";
import useFetch from "../../CustomHooKs/useFetch";

export default function Location({ url }) {
  //const data = useFetch(url);
  const [data, setData] = useState(null);
  const [location, setLocation] = useState({
    city: "",
    country: "",
  });

  const loadData = async (url) => {
    const apiRes = await fetch(url);
    const resJSON = await apiRes.json();
    return resJSON;
  };

  useEffect(() => {
    loadData(url).then((res) => {
      setLocation({
        city: res.city,
        country: res.country_name,
      });
    });
  }, [url]);

  /*   if (data === null) {
    return (
      <div className="geo-information">
        <div className="location">
          <p className="loc-info">Loading...</p>
        </div>
        {<Weather url="" />}
      </div>
    );
  } else { */
  //alert(JSON.stringify(data));
  return (
    <div className="geo-information">
      <div className="location">
        <p className="loc-info">{location.city}</p>
        <p className="loc-info">{location.country}</p>
      </div>
      <Weather
        url={`https://api.openweathermap.org/data/2.5/weather?q=Damascus, SY&units=metric&APPID=b375cf092bf8ec54a7b05c1f95e8747c`}
      />
    </div>
  );
  // }${data.country_capital}, ${data.country_code2}
}
