import React, { useState } from "react";

import "./time.styles.scss";

export default function Time() {
  const [time, setTime] = useState({
    hour: "",
    minute: "",
    second: "",
    ampm: "",
  });

  setInterval(() => {
    let now = new Date();
    setTime({
      hour:
        now.getHours() === 0
          ? 12
          : now.getHours() > 12
          ? now.getHours() - 12
          : now.getHours(),
      minute: now.getMinutes(),
      second: now.getSeconds(),
      ampm: now.getHours() < 12 ? "AM" : "PM",
    });
  }, 1000);

  return (
    <div className="time">
      <p className="unit">{("00" + time.hour).slice(-2)}</p>
      <p className="unit">{("00" + time.minute).slice(-2)}</p>
      <p className="unit">{("00" + time.second).slice(-2)}</p>
      <p className="unit">{("--" + time.ampm).slice(-2)}</p>
    </div>
  );
}
