import React, { useState } from "react";

import "./loadpage.styles.scss";

import Location from "../../components/location/location.component";
import Time from "../../components/time/time.component";
import SignIn from "../../components/sign-in/sign-in.component";
import defaultImage from "../../assets/images/flash.jpg";

export default function LoadPage() {
  const [user, setUser] = useState(localStorage.getItem("user"));
  var imageUrl = defaultImage;

  const handleLogin = (email, password) => {
    /* localStorage.setItem("email", email);
    localStorage.setItem("password", password); */
    localStorage.setItem("imageUrl", imageUrl);
    localStorage.setItem("user", email.substr(0, email.indexOf("@")));
    setUser(localStorage.getItem("user"));
  };

  if (user === null) {
    return (
      <div className="loadpage">
        <SignIn handleLogin={handleLogin} />
      </div>
    );
  } else {
    return (
      <div className="loadpage">
        <h2 className="title">Welcome {user}</h2>
        <Time />
        <Location url="https://api.ipgeolocation.io/ipgeo?apiKey=8fa116c08bf641e9b988b1db30968a52" />
      </div>
    );
  }
}
